<style lang='scss'>
  #searchComplete{
    position: absolute;
    left: 15px;
    top: 15px;
    z-index: 2;
    #searchInput{
      overflow: hidden;
      >input{
        float:left;
        width: 260px;
        height: 40px ;
        line-height: 40px;
        border-radius: 0;
        border: 0;
        padding:0 10px;
        color: #0c1a2c;
        font-size: 14px;
      }
      .searchIcon{
        float: left;  
        height: 40px;
        line-height: 46px;
        width: 40px;
        background-color: #fff;
        cursor: pointer;
        text-align: center;
        color: #0c1a2c;
      }
    }
    #searchPool{
      width: 100%;
      height: 316px;
      overflow: hidden; 
      background-color: #fff;
      border-top: 1px solid #ddd;
      li{
        cursor: pointer;
        height: 35px;
        line-height: 35px;
        text-indent: 1.5em;
        font-size: 14px;
        &:hover{
          background-color: #ddd;
        }
      }
      p{
        height:100%;
        width: 100%;
        .fa-icon{
          font-size:30px;
          position: relative;
          left: 45%;
          top:38%;
        }
      }
    }
  }
</style>
<template>
  <div id="searchComplete">
    <p id="searchInput">
      <input type="text" v-model="keywords" placeholder="请输入IP、机构、ASN号进行查询" @keyup="autoComplete()" >
      <span class="searchIcon" @click="search()">
        <icon name="search"></icon>
      </span>
    </p>
    <div id="searchPool" v-show="searching">
      <p v-if="loadingComplete">
        <icon name="spinner" pulse></icon>  
      </p>
      <ul v-if="!loadingComplete">  
        <li @click="selectComplete('中国移动')" >
          <span>中国移动 </span><span> 北京市海淀区</span>
        </li>
        <li @click="selectComplete('中国联通')">
          <span>中国联通 </span><span> 北京市海淀区</span>
        </li>
        <li @click="selectComplete('中国电信')">
          <span>中国电信 </span><span> 北京市海淀区</span>
        </li>
        <li @click="selectComplete('中关村软件园一期')">
          <span>中关村软件园一期 </span><span> 北京市海淀区</span>
        </li>
        <li @click="selectComplete('中关村软件园二期')">
          <span>中关村软件园二期 </span><span> 北京市海淀区</span>
        </li>
        <li @click="selectComplete('中关村软件园三期')">
          <span>中关村软件园三期 </span><span> 北京市海淀区</span>
        </li>
        <li @click="selectComplete('中关村软件园四期')">
          <span>中关村软件园四期 </span><span> 北京市海淀区</span>
        </li>
        <li @click="selectComplete('中关村软件园五期')">
          <span>中关村软件园五期 </span><span> 北京市海淀区</span>
        </li>
        <li @click="selectComplete('中关村软件园六期')">
          <span>中关村软件园六期 </span><span> 北京市海淀区</span>
        </li>
      </ul> 
    </div>
     
  </div> 
</template>
<script type="text/ecmascript-6">
    import {mapGetters} from 'vuex';
    import debounce from 'debounce';
    import http from '../../../../../assets/js/http';
    export default {
      data () {
        return {
          keywords: '',
          searching: false,
          loadingComplete: false
        };
      },
      computed: {
      },
      methods: {
        search: function () {
          if (this.keywords === '' || typeof this.keywords === 'undefined'){
            this.$message.error('关键字不能为空');
            return;
          }
          this.$store.dispatch('changeAbilityKeyword', this.keywords)
        },
        getAutoCompleteData () {
          this.loadingComplete = true;
          setTimeout(() => {
            this.loadingComplete = false;
          }, 500);
          // this.apiGet('').then(
          //   res => {
          //     this.loadingComplete = false;
          //   }
          // )
        },
        autoComplete: debounce(function () {
          this.searching = true;
          this.getAutoCompleteData();
        }, 600),
        selectComplete (str) {
          this.keywords = str;
          this.searching = false;
        }
      },
      mounted () {
      },
      mixins: [http]
    }
</script>



